.basket_main {
  position: fixed;
  background-color: #000;

  bottom: 0;
  right: 0;
  left: 0;
  height: 100%;

  overflow-y: auto;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  cursor: pointer;
}

.basket_visible {
  animation: basketIn 0.5s normal ;
}

.basket_hidden {
  animation: basketOut 0.7s normal ;
  bottom: -1200px;
}

.basket_none {
  display: none;
}

@keyframes basketIn {
  0% {
    bottom: -1200px;
  }
  100% {
    bottom: 0;
  }
}

@keyframes basketOut {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -1200px;
  }
}

hr {
  margin: 10px;
}

.basket-title {
  margin-top: 15px;
  padding: 10px;
  color: #fff;
  background-color: #191917;
  height: 50px;

  border-radius: 20px;

  display: flex;
  justify-content: center;
  align-items: center;
}
 
h3 {
  font-size: 20px;
}

.basket-title img {
  cursor: pointer;
  position: absolute;

  top: 38px;
  left: 15px;
}

.basket-content {
  color: #fff;
  background-color: #191917;

  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  height: auto;
}

.basket-content-title {
  color: #fff;
  margin-top: 8px;

  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}


.basket-content-orders {
  margin: 0 auto;
}

.orders-container-basket {
  height: auto;
  background-color: #191917;;

  padding-bottom: 90px;
  padding-left: 10px;
  padding-right: 10px;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
}


@media (min-width: 350px) {
  .orders-container-basket {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }
}