
.product_description {
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  
  background-color: #101010;
  cursor: pointer;
  position: fixed;
  z-index: 1000;
  bottom: 0;
  width: 100%;
  color: var(--white);

  animation: slideInDescription 0.3s forwards; 
}

@keyframes slideOutDescription {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -1300px;
  }
}

@keyframes slideInDescription {
  0% {
    bottom: -800px;
  }
  100% {
    bottom: 0;
  }
}

.visible_description {
  animation: slideInDescription 0.4s normal ;
}

.hidden_description {
  animation: slideOutDescription 0.4s normal ;
  bottom: -1900px;
}

.product_description_title{
  padding: 10px;
}

.product_description_title p {
  font-size: 14px;
  color: #565555;
  margin-bottom: 0px;
}

.product_description_desc  {
  padding-bottom: 10px;
}

.product_description_price {
  margin-bottom: 10px;
}

.close_product_description_btn {
  position: absolute;
  top: 10px;
  right: 20px;

  background-color: none;
  border: none;
  color: var(--white);
}

.close_product_description_img {
  width: 25vh;
  height: 25px;
}

.product_description img {
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
  width: 99%;
  height: auto;
}

.product_description_btns_container {
  background-color: #373737; 
}

.product_description_btns_title {
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
}

.product_description_btns_title_name {
  display: inline-block;
}

.product_description_btns_title_weight {
  font-size: 14px;
  color: #8d8d8d;
  margin-bottom: 0px;
}

.product_description_btns {
  padding-top: 10px;
  
  display: grid;
  
  gap: 15px;
  grid-template-columns: 1fr 2fr;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
}

.product_description_counter_btns {
  padding-left: 10px;
  padding-right: 10px;
  color: var(--white);
  font-size: 24px;

  border-radius: 20px;
  background-color: var(--order-btn);

  display: grid;
  justify-content: center;
  align-self: center;
  
  height: 55px;
  border-radius: 20px;

  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
}

.product_description_counter_btns span {
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-self: center;
}

.product_description_counter_btns div {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3px;
}

.product_description_add_btns {
  border: none;
  border-radius: 20px;
  font-weight: bold;
  color: var(--black);
  font-size: 16px;
  background-color: rgb(248, 248, 5);
}
