.basket-order-main {
  display: flex; 
  justify-content: space-between; 
  align-items: center; 
  padding: 10px; 
}

.basket-order-content {
  display: flex;

  align-items: center;
}

.basket-order-desc {
  
}

.basket-order-img {
  border-radius: 10px;

  width: 13vh;
  height: 10vh;
  color: #fff;
  margin-right: 10px;
}

.basket-order-prices {
  display: flex;
  font-size: 16px;
}

.basket-order-prices p {
  margin: 0;
  margin-right: 5px;
}

.basket-weight {
  color: #686866;
}

.basket-order-counter {
  background-color: #2f2f2d;
  display: grid;

  gap: 20px;
  padding: 10px 20px;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  border-radius: 12px;
}

.basket-order-counter span {
  justify-content: center;
  text-align: center;
}

.basket-order-counter div {
  justify-content: center;
  text-align: center;
}

.basket-smth-else {
  height: 100vh;
}

@media (min-width: 700px) {
  .basket-order-img {
    width: 200px;
    height: auto;
  }
}