.main-payment {
  margin-top: 10px;
  color: var(--white);
  background-color: var(--black);
  height: 100vh;

  position: fixed;

  bottom: 0;
  right: 0;
  left: 0;
  height: 100%;

  overflow-y: auto;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  cursor: pointer;
 
}

.payment_block {
  display: none;
}


.payment_visible {
  animation: paymentIn 0.4s normal ;
}

.payment_hidden {
  animation: paymentOut 0.6s normal ;
  bottom: -2000px;
}

.payment_none {
  display: none;
}

@keyframes paymentIn {
  0% {
    bottom: 2000px;
  }
  100% {
    bottom: 0;
  }
}

@keyframes paymentOut {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -2000px;
  }
}

.payment-h1 {
  margin-top: 10px;
}

.type-order-container {
  padding: 10px;
}

.payment-title {
  background-color: #191917;
  border-radius: 23px;

  padding-top: 10px;
  padding-left: 10px;

  margin-top: 15px;
}

.payment-title img {
  padding-left: 10px;
}

.type-order {
  border: #1f1f1d 3px solid;
  background-color: #191917;
  padding: 20px;
  display: inline-block;
  border-radius: 14px;

  margin-right: 10px;
}

.type-order div {
  margin-top: 3px
}

.type-order div span {
  font-weight: 600;
  font-size: 16px;
}

.active-order {
  background-color: #1f1f1d;
}

.radio-btn {
  background-color: #1f1f1d;
  padding: 10px;
  width: 15px;
  height: 15px;
  border-radius: 50px;

  margin-bottom: 23px;
}

.active-btn {
  background-color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
}

.active-img {
  margin-right: 8px;
}

.disactive-img {
  display: none;
}

.payment-address-container {
  padding-top: 2px;
  padding-left: 15px;
  margin-top: 5px;

  background-color: #191917;
  border-radius: 23px;
}

.payment-address {
  display: flex;
  justify-content: left;
  align-items: center;

  padding-bottom: 20px;
}

.concret-adress {
  display: grid;
  padding-left: -15px;
  grid-template-columns: repeat(2, minmax(120px, 180px)) ;
  gap: 10px;

  margin-bottom: 30px;
}

.payment-comment {
  display: flex;
  margin-bottom: 20px;
}

.payment-msg {
  padding-bottom: 20px;
}

.payment-amount {
  background-color: #191917;
  border-top-left-radius: 23px;
  border-top-right-radius: 23px;

  padding-top: 2px;
  padding-left: 15px;
  margin-top: 5px;
  padding-bottom: 5vh
}

.payment-amount div {
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding-right: 10px;
}

.payment-amount div p {
  margin: 10px;
}

.temp-div {
  height: 10vh;
  background-color: var(--black);
}

.payment-btn {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  display: block;

  background-color: #42413d;

  display: grid;
  grid-template-columns: 120px 1fr;
  padding: 0 13px;
  align-items: center;

  padding-bottom: 20px;
}

.payment-btn p {
  text-align: center;
  font-size: 24px;

  font-weight: 600;
}

.payment-order-btn {
  background-color: #fde000;
  color: var(--black);

  font-weight: 600;
  font-size: 18px;

}

.payment-order-btn-hidden {
  display: none;
}

.payment-order-btn-visible {
  display: grid !important;
}
