.main_header {
  z-index: 9998;
}

.menu-categories {
  white-space: nowrap;
  padding: 10px 0;
  overflow-x: auto;
  scrollbar-width: none; 
  -ms-overflow-style: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 15px;
}

.menu-categories::-webkit-scrollbar {
  display: none; 
}

.category-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
}

.category-list li {
  margin-right: 10px;
}

.category-list button {
  padding: 8px 16px; 
  font-size: 17px;
  color: #5b5b59; 
  font-weight: bold;
  background-color: var(--main-bg-color);
  border: none;
  border-radius: 0;
  cursor: pointer;
  transition: all 0.4s; 
}

.category-list button.active {
  color: #cdcbc8;
  background-color: #2f2f2f;
  border-radius: 12px;
}

.right_slide {
  animation: slideRightContent 0.4s forwards; 
}

@keyframes slideRightContent {
  0% {
    right: 0;
  }
  100% {
    right: 800px;
  }
}