
.total_sum {
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  cursor: pointer;
  position: fixed;

  bottom: 0; 
  left: 0;
  right: 0;

  z-index: 999;
  height: 55px;
  display: flex;

  justify-content: center;
  align-items: center;
  background-color: #d4be14;
  margin: 10px 5px;
  padding: 8px;
  border-radius: 25px;
}

.visible {
  animation: btnIn 0.4s normal ;
}

.hidden {
  animation: btnOut 0.4s normal ;
  bottom: -200px;
}


@keyframes btnIn {
  0% {
    bottom: -100px;
  }
  100% {
    bottom: 0;
  }
}

@keyframes btnOut {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -200px;
  }
}

.total_sum p {
  flex: 1; 
  text-align: center; 
  font-size: 16px;
  font-weight: bold;
}

.total_sum span {
  font-size: 16px;
  font-weight: bold;
}