.order-container {
  background-color: #302f2d;
  display: inline-block;
  padding-top: 5px;
  border-radius: 25px;
  margin: 0;
}

.order-descript {
  height: 75px;
  margin: 0;
  padding-left: 10px;
  padding-top: 5px;
} 

.order-title {
  color: var(--white);
  margin: 0; 
}

.order-weight {
  color: #5b5957;
  font-size: 14px;
  margin-top: 10px; 
}

.order-btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--order-btn);
  border: none;
  color: var(--white);
  text-align: center;
  height: 55px;
  border-radius: 20px;
  margin: 5px;
}

.order-img {
  width: 96%; 
  height: auto; 
  border-radius: 25px 25px 10px 10px;
  display: block;
  margin: 0 auto;
}

.order-count-btns {
  display: grid;
  background-color: #504f4f;

  height: 55px;
  border-radius: 20px;

  margin: 5px;
  gap: 10px;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
}

.order-count-btns div {
  cursor: pointer;
  text-align: center;
  color: var(--white);
  font-size: 20px;
}

.order-count-btns span {
  text-align: center;
  color: var(--white);
  font-size: 18px;
}
